@import "../../style.scss";

.regisOder{
    @include themify($themes){
    flex:3;
    position: sticky;
    top: 50px;
    height: 100%;
    overflow: scroll;
    background-color: themed("bgSoft");
    @include mobile{
        display: none;
    }
    @include tablet{
        display: none;
    }
    &::-webkit-scrollbar{
        display: none;
    }
    .container{
        padding: 20px;
        .item{
            -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
            -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
            box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
            padding: 20px;
            margin-bottom: 20px;
            background-color: themed("bg");
            margin-top: 28px;
            border-radius: 20px;

            span{
                color: gray;
            }
            .user{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 20px 0px;
               
                .userInfo{
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    position: relative;
                    img{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    .online{
                        width: 12px;
                        height: 12px;
                        border-radius: 50%;
                        background-color: limegreen;
                        position: absolute;
                        top: 0;
                        left: 30px;
                    }
                    p{
                        color: themed("textColorSoft");
                    }
                    span{
                        font-weight: 500;
                        color: themed("textColor");
                    }
                }
                .buttons{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    button{
                        border: none;
                        padding: 5px;
                        color: white;
                        cursor: pointer;
                        background-color: #5271ff;
                        
                    }
                }
            }
        }
    }
}
}